.header__container {
  display: grid;
  grid-template-columns: 286px auto auto;
  padding: 12px 18px 8px 24px; }

.logo {
  @include default-font(37px);
  color: rgb(0, 2, 3);
  line-height: 1.2;
  text-align: left; }

.header__search {
  //display: grid
  //grid-template-columns: auto auto auto
  display: flex; }


.header__searcharea {
  width: calc(100% - 78px);
  display: flex; }

.search {
  @include default-font(12px);
  border: 1px solid #dcdee0;
  color: rgb(46, 50, 54);
  line-height: 1.2;
  padding: 8px 1px 7px 12px;
  height: 32px;
  margin-top: 6px;
  width: 100%; }


.search__icon {
  margin-top: -9px;
  margin-left: -1px; }

.icon-search {
  border: 1px solid #dcdee0;
  height: 32px;
  width: 32px;
  margin-top: 15px; }

.icon-search, .icon-advanced-settings {
  fill: #53595e;
  font-size: 22px; }

.search__settings {
  margin-top: -17px; }

.search__settings a {
  text-decoration: none;
  @include default-font(12px);
  color: rgb(10, 79, 139);
  line-height: 1.2; }


.header__account {
  display: flex;
  justify-content: end; }

.icon-home {
  fill: #878c8f;
  margin-right: 11px; }

.header__account .icon-home {
  background: #dcdee0;
  border-radius: 36px;
  padding: 10px;
  font-size: 35px;
  margin-top: 7px; }

.header__accountname {
  text-decoration: none;
  @include default-font(12px);
  color: rgb(46, 50, 54);
  margin-top: 17px; }


.header__navline {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #dcdee0;
  font-size: 12px; }

.header__nav a {
  margin-right: 18px; }

.header__nav a, .header__background-jobs a {
  text-decoration: none;
  text-transform: uppercase;
  @include default-font(12px);
  color: rgb(46, 50, 54);
  line-height: 4.167; }

.header__nav .is-active {
  background-color: #383e42;
  padding: 18px 45px 18px 37px;
  color: white; }

.header__background-jobs {
  text-align: right;
  margin-right: 23px; }

.icon-background-jobs {
  fill: rgb(46, 50, 54); }

.header__background-jobsnum {
  background-color: #ff5f4b;
  padding: 0px 7px 1px 6px;
  border-radius: 11px;
  color: white;
  margin-left: 15px; }

@media (max-width: 640px) {
  .header__nav a {
    margin-right: 10px; } }


@media (max-width: 1020px) {
  .header__logo, .header__search, .header__account {
    grid-column: 1 / 4; }

  .search__settings {
    margin-top: 11px; }

  .header__navitemname {
    display: none; }

  .header__background-jobs {
    text-align: left; } }
